.productCard {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    max-width: 270px;
    position: relative;
    width: 100%;

    @media(max-width: 1200px) {
        max-width: 100%;
    }

    &__info {
        display: flex;
        height: 350px;
        flex-direction: column;
        justify-content: space-between;
        margin-bottom: 10px;
        overflow: hidden;
        width: 100%;

        @media(max-width: 1200px) {
            height: 450px;
        }

        .img {
            display: block;
            height: 350px;
            max-width: 100%;
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;

            @media(max-width: 1200px) {
                height: 450px;
            }
            
            &::after {
                content: '';
                background-color: rgba(#000, .1);
                position: inherit;
                left: inherit;
                top: inherit;
                height: 100%;
                width: 100%;
            }

            img {
                object-fit: contain;
                object-position: top;
                height: 100%;
                width: 100%;
                
                @media(max-width: 1200px) {
                    object-fit: cover;
                }
            }
        }

        span {
            background-color: var(--background);
            // border: solid 1px var(--primary);
            color: var(--primary-text);
            display: inline-block;
            font-family: 'Outfit', sans-serif;
            font-size: 12px;
            font-weight: 300;
            letter-spacing: 1.2px;
            margin: 10px 0 0 0;
            padding: 4px 8px;
            position: relative;
            width: fit-content;
            z-index: 1;
        }

        .btnsControl {
            bottom: -50px;
            display: flex;
            align-items: center;
            margin-top: auto;
            position: relative;
            transition: all .3s ease-in-out;
            z-index: 1;

            &.active {
                bottom: 0;
            }

            @media(max-width: 1200px) {
                bottom: 0;
            }
            
            .btnBuy, .btnFav, .btnView {
                background-color: rgba(var(--terciary-rgb), .3);
                border-top: solid 1px #ededed;
                color: #fff;
                display: flex;
                align-items: center;
                justify-content: center;
                height: 40px;
                text-align: center;
                text-decoration: none;
                transition:  all .3s ease-in-out;
                width: 15%;

                i {
                    display: inline-block;
                    font-size: 18px;
                    vertical-align: middle;
                }

                &:hover {
                    background-color: var(--terciary);
                }

                @media(max-width: 1200px) {
                    background-color: var(--terciary);
                }
            }

            .btnFav {
                border-left: solid 1px #ededed;
                border-right: solid 1px #ededed;
            }

            .btnBuy {
                font-family: "Outfit";
                font-size: 12px;
                text-align: center;
                width: 70%;
                
                i {
                    margin-left: 5px;
                }
            }

        }

    }
    h3 {
        a {
            color: var(--primary);
            display: inline-block;
            text-decoration: none;
        }
    }
    p {
        color: var(--primary-text);
        font-family: 'Outfit', sans-serif;
    }
}

.release {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;

    @media(max-width: 1200px) {
        .productCard {
            &:not(:first-child) {
                display: none;
            }
        }
    }
}

.productInfos {
    display: grid;
    grid-template-columns: 1fr 1fr;

    @media(max-width: 1200px) {
        display: flex;
        flex-direction: column-reverse;
    }

    &__img {
        display: grid;
        gap: 20px;
        grid-template-columns: auto 1fr;
        grid-template-rows: 180px 180px 180px;
	    grid-template-areas:
		"miniature main";

        @media(max-width: 1200px) {
            gap: 10px;
            grid-template-columns: auto;
            grid-template-rows: auto auto;
            grid-template-areas:
            "main"
            "miniature";
        }

        img {
            height: 580px;
            object-fit: cover;
            max-width: 430px;
            width: 100%;

            &:not(.main) {
                object-fit: cover;
                object-position: center;
                height: 180px;
                max-width: 100%;
                width: 140px;

                @media(max-width: 1200px) {
                    height: 150px;
                    width: calc(32% - 5px);
                }
            }
        }

        .main {
            grid-area: main;
        }

        .miniature {
            grid-area: miniature;
            display: flex;
            flex-direction: column;
            gap: 20px;

            @media(max-width: 1200px) {
                flex-direction: row;
                flex-wrap: wrap;
                gap: 10px;
                justify-content: space-between;
            }
        }
    }

    &__txt {
        padding-left: 40px;
        max-width: 480px;

        @media(max-width: 1200px) {
            padding: 0;
            margin-bottom: 30px;
        }

        h2 {
            color: var(--primary);
            font-size: 36px;
            line-height: 1.2;
            margin-bottom: 5px;
        }

        p.price {
            color: var(--primary-text);
            font-family: 'Outfit', sans-serif;
            font-size: 24px;
            font-weight: 300;
            margin-bottom: 20px;
        }

        p.description {
            color: var(--primary-text);
            font-family: 'Outfit', sans-serif;
            font-size: 18px;
            font-weight: 400;
            margin-bottom: 40px;
        }

        .btnProduct {
            display: flex;
            align-items: center;
            justify-content: center;
            position: relative;
            transition: all .3s ease-in-out;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: 100%;
            z-index: 99;

            @media(max-width: 1200px) {
                margin-top: 30px;
            }
           

            &.fixed {
                position: fixed;
                bottom: 20px;
               
            }

            button {
                background-color: var(--terciary);
                border: 0;
                color: #fff;
                cursor: pointer;
                font-family: 'Outfit', sans-serif;
                font-size: 18px;
                height: 60px;
                margin-left: 2px;
                outline: none;
                padding: 0 65px;
                transition: all .3s ease-in-out;

                &:hover {
                    background-color: var(--terciary-hover);
                }

                @media(max-width: 1200px) {
                    font-size: 16px;
                    padding: 0 20px;
                }

                i {
                    display: inline-block;
                    margin-left: 5px;
                }
            }

            &__qtd {
                display: flex;
                align-items: center;

                button {
                    padding: 0;
                    margin: 0;
                    width: 30px;
                }

                input {
                    background-color: var(--terciary);
                    border: 0;
                    color: #fff;
                    font-family: 'Outfit', sans-serif;
                    font-size: 18px;
                    height: 60px;
                    outline: none;
                    text-align: center;
                    width: 40px;
                }
            }
        }

        .categs {
            display: flex;
            margin-bottom: 20px;

            strong {
                color: var(--primary-text);
                font-size: 18px;
            }

            ul {
                align-items: center;
                display: flex;
                gap: 10px;
                margin-left: 15px;
                
                li {
                    &:not(:last-child) {
                        align-items: center;
                        display: flex;

                        &:after {
                            content: ',';
                            color: var(--primary-text);
                            font-size: 16px;
                            line-height: 1;
                        }
                    }

                    a {
                        color: var(--primary-text);
                        display: block;
                        font-family: 'Outfit', sans-serif;
                        font-size: 16px;
                        font-weight: 400;
                        line-height: 1.2;
                        position: relative;
                        text-decoration: none;

                        &::after {
                            content: '';
                            background-color: var(--primary-text);
                            bottom: -10px;
                            height: 1px;
                            left: 0;
                            position: absolute;
                            transition: width .3s ease-in-out;
                            width: 0;
                        }

                        &:hover {
                            &::after {
                                width: 100%;
                            }
                        }
                    }
                }
            }

        }

        .tags {
            display: flex;
            margin-bottom: 50px;

            @media(max-width: 1200px) {
                margin-bottom: 0;
            }

            strong {
                color: var(--primary-text);
                font-size: 18px;
            }

            ul {
                align-items: center;
                display: flex;
                gap: 5px;
                margin-left: 15px;
                
                li {
                    background-color: var(--hover);
                    

                    a {
                        color: var(--primary-text);
                        display: block;
                        font-family: 'Outfit', sans-serif;
                        font-weight: 400;
                        line-height: 1.2;
                        padding: 10px 15px;
                        text-decoration: none;
                        transition: all .3s ease-in-out;

                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }
        }
    }
}