.blog {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 50px;

    @media(max-width: 600px) {
        flex-direction: column;
    }

    &__post {
        display: flex;
        flex-direction: column;
        max-width: 380px;
        position: relative;
        width: 100%;

        @media(max-width: 768px) {
            max-width: 32%;
        }

        @media(max-width: 600px) {
            max-width: 380px;
            
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }

        &-img {
            display: block;
            height: 280px;
            margin-bottom: 15px;
            position: relative;
            width: 100%;

            @media(max-width: 768px) {
                height: 200px;
            }

            &::after {
                content: '';
                background-color: rgba(#000, .5);
                height: 100%;
                left: 0;
                position: absolute;
                top: 0;
                transition: background .3s ease;
                width: 100%;
            }

            &:hover {
                &::after {
                    background-color: rgba(#000, .7);
                }
            }

            strong {
                background-color: var(--background);
                color: var(--primary-text);
                font-size: 22px;
                line-height: 1;
                left: 10px;
                padding: 6px 12px;
                position: absolute;
                text-align: center;
                top: 10px;
                z-index: 3;

                span {
                    display: block;
                    line-height: 1;
                    font-size: 16px;
                    font-family: 'Outfit', sans-serif;
                    font-weight: 400;
                }
            }

            img {
                display: block;
                height: 100%;
                object-fit: cover;               
                width: 100%;
            }
        }

        ul {
            display: flex;
            li {
                &:not(:last-child) {
                    margin-right: 5px;
                }
                a {
                    color: var(--primary-text);
                    display: block;
                    font-family: 'Outfit',sans-serif;
                    font-size: 14px;
                    margin-bottom: 5px;
                    text-decoration: none;
                    transition: all .3s ease-in-out;

                    &:hover {
                        background-color: var(--background);
                    }
                }
            }
        }

        h2 {
            font-size: 22px;
            line-height: 1.2;
            margin-bottom: 10px;

            @media(max-width: 768px) {
                font-size: 16px;
            }

            @media(max-width: 600px) {
                font-size: 18px;
            }

             a {
                color: var(--primary);
                font-family: "Plus Jakarta Sans", sans-serif;
                font-weight: 400;
                font-optical-sizing: auto;
                text-decoration: none;
                transition: all .3s ease-in-out;

                &:hover {
                    color: var(--secondary);
                }
             }
        }

        p {
            color: var(--primary-text);
            font-family: 'Outfit', sans-serif;
            font-size: 16px;
            font-weight: 300;
            line-height: 1.5;

            @media(max-width: 768px) {
                font-size: 14px;
            }
        }
    }

    &__btn {
        display: block;
        margin: 0 auto 100px;
        width: max-content;
    }
}

.blogPage {
    padding: 50px 0 100px;
    @media(max-width: 1200px) {
        padding: 0 0 100px;

        >.container:not(:last-child) {
            width: 100%;
        }
    }

    .blog {
        align-items: baseline;

        &__post {
            &.main {
                flex-direction: row;
                max-width: 100%;
                position: relative;
                width: 100%;
                
                @media(max-width: 1200px) {
                    height: calc(100vh - 80px);
                    margin-bottom: 30px;
                    padding: 20px;
                }
    
                .blog__post-img {
                    height: 70vh;
                    width: 80%;
                    margin-bottom: 0;

                    @media(max-width: 1200px) {
                        height: 100%;
                        width: 100%;
                        position: absolute;
                        left: 0;
                        top: 0;

                        &::after {
                            background-color: rgba(#000, .7);
                        }

                        strong {
                            left: 20px;
                            top: 20px;
                        }
                    }
                }

                .blog__post-txt {
                    align-items: baseline;
                    justify-content: center;
                    display: flex;
                    flex-direction: column;
                    max-width: 550px;
                    padding: 0 30px;
                    position: relative;

                    @media(max-width: 1200px) {
                        align-items: center;
                        padding: 0;
                    }

                    h2 {
                        font-size: 36px;

                        @media(max-width: 1200px) {
                            font-size: 28px;

                            a {
                                font-weight: 600;
                            }
                        }
                    }

                    p {
                        font-size: 20px;
                        margin-bottom: 30px;
                        @media(max-width: 1200px) {
                            color: #fff;
                            font-size: 18px;
                        }
                    }
                }
            }
    
            &:nth-child(-n+3) {
                margin-bottom: 50px;
            }
        }
    }
}

.singlePost {
    align-items: center;
    display: flex;
    flex-direction: column;
    padding: 50px 0 100px;
    margin: 0 auto;
    max-width: 900px;

    h1 {
        color: var(--primary);
        font-size: 48px;
        line-height: 1.2;
        margin-bottom: 20px;
        text-align: center;

        @media(max-width: 1200px) {
            font-size: 32px;
        }
    }

    h2 {
        color: var(--primary-text);
        font-family: 'Outfit', sans-serif;
        font-size: 22px;
        font-weight: 300;
        margin-bottom: 20px;
        text-align: center;

        @media(max-width: 1200px) {
            font-size: 18px;
        }
    }

    strong  {
        font-family: 'Outfit',sans-serif;
        color: var(--primary-text);
        font-size: 16px;
        margin-bottom: 10px;
    }

    ul {
        display: flex;
        margin-bottom: 30px;
        @media(max-width: 1200px) {
            margin-bottom: 15px;
        }
        li {
            &:not(:last-child) {
                margin-right: 5px;
            }
            a {
                color: var(--primary-text);
                display: block;
                font-family: 'Outfit',sans-serif;
                font-size: 14px;
                margin-bottom: 5px;
                text-decoration: none;
                transition: all .3s ease-in-out;

                &:hover {
                    background-color: var(--background);
                }
            }
        }
    }

    .thumb {
        display: block;
        height: 400px;
        width: 100%;
        margin: 0 auto 50px;
        object-fit: contain;
        @media(max-width: 1200px) {
            height: auto;
            margin-bottom: 30px;
        }
    }

    p {
        font-family: 'Outfit', sans-serif;
        font-size: 18px;
        font-weight: 300;
        line-height: 1.5;
    }

    img:not(.thumb){
        display: block;
        margin: 30px auto;
        max-width: 100%;
        width: auto;
    }
}